<template>
  <div class="table-striped table-responsive">
    <table class="table mb-0 text-center">
      <thead>
        <tr>
          <th style="width: 5%" scope="col">No</th>
          <th style="width: 15%" scope="col">Kode MK</th>
          <th style="width: 50%" scope="col">Nama MK</th>
          <th scope="col">Sks</th>
          <th scope="col">Nilai</th>
          <th scope="col">NxK</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(curiculum7, index) in getDataKuri7.courses"
          :key="curiculum7.course_code"
        >
          <td>{{ number(index) }}</td>
          <td>{{ curiculum7.course_code }}</td>
          <td class="text-left">{{ curiculum7.name_ina }}</td>
          <td>{{ curiculum7.credits }}</td>
          <td>{{ curiculum7.grade }}</td>
          <td>{{ curiculum7.nk }}</td>
        </tr>
      </tbody>
    </table>
    <div class="p-2 mb-2 bg-biru-accent text-dark btn-block">
      Total IP (Indeks Prestasi)
      <p class="float-right mr-4">{{ getDataKuri7.ip }}</p>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Semester7Kuri",
  data() {
    return {
      curiculum7: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataKuri7: "kurikulum/kurikulum7",
    }),
  },
  methods: {
    ...mapActions({
      kurikulums: "kurikulum/kurikulum",
    }),
    number: function (index) {
      return index + 1;
    },
    getCurriculuum() {
      this.kurikulums();
    },
  },
  mounted() {
    this.getCurriculuum();
  },
};
</script>
