<template>
  <div class="table-striped table-responsive">
    <table class="table mb-0 text-center" >
      <thead>
        <tr>
          <th style="width: 5%" scope="col">No</th>
          <th style="width: 15%" scope="col">Kode MK</th>
          <th style="width: 50%" scope="col">Nama MK</th>
          <th scope="col">Sks</th>
          <th scope="col">Nilai</th>
          <th scope="col">NxK</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(curiculum, index) in getDataKuri.courses"
          :key="curiculum.course_code"
        >
          <td>{{ number(index) }}</td>
          <td>{{ curiculum.course_code }}</td>
          <td class="text-left">{{ curiculum.name_ina }}</td>
          <td>{{ curiculum.credits }}</td>
          <td>{{ curiculum.grade }}</td>
          <td>{{ curiculum.nk }}</td>
        </tr>
      </tbody>
    </table>
    <div class="p-2 mb-2 bg-biru-accent text-dark btn-block">
      Total IP (Indeks Prestasi)
      <p class="float-right mr-4">{{ getDataKuri.ip }}</p>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Semester1Kuri",
  data() {
    return {
      curiculum: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataKuri: "kurikulum/kurikulum",
      getCetak: "cetakKHS/datacetak",
    }),
  },
  methods: {
    ...mapActions({
      kurikulums: "kurikulum/kurikulum",
      cetak_khs: "cetakKHS/cetakkhs",
    }),
    number: function (index) {
      return index + 1;
    },
    getCurriculuum() {
      this.kurikulums();
    },
    getCetakKHS() {
      this.cetak_khs();
    },
  },
  mounted() {
    this.getCurriculuum();
    this.getCetakKHS();
    
  },
};
</script>
